.modal {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
}

.modal-content {
  width: 500px;
  background-color: #fff;
  padding: 50px;
}

.modal-header,
.pmodal-footer {
  padding: 50px;
  margin-top: 5rem;
}

.pmodal-body {
  margin: 0;
  padding: 10px;
  border-top: 1px solid #eee;
  border-bottom: 1px solid #eee 1;
}
